/* Footer.css */

.signature {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Martian Mono', monospace;
  font-size: 9px;
  color: #022C56;
  margin-top: 10px;
  margin-bottom: 40px;
}

.signature div:first-of-type {
  margin-bottom: 7px;
}

.signature div:last-of-type {
  font-size: 9px;
  margin-top: 5px;
 
}
